* { 
    font: 400 20px Dosis;
    color:black;
}

body {
    background-image: url(/my-website/static/media/bg5.66cd1bd9.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat scroll;
    background-size: cover;
    background-position: center center;
}

.menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-left: 15%;
    margin-right: 15%;
}

.menu button {
    color: white;
    font:470 1.5vw Playfair Display, sans-serif;
    height: 9vh;
    width:12vw;
    padding-top: 3vh;
    padding-bottom: 3vh;
    background: fixed;
}
.menu button:hover{
    color: black;
    font: 700 1.5vw Playfair Display, bold;
    -webkit-filter: contrast(100%);
            filter: contrast(100%);
    text-shadow: 0.2vh 0.3vh 4vw white;
}

button {
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
    cursor: pointer;
    border: none;
    /*
    border:1px solid brown;*/
    outline:none;
}

button:hover {
    -webkit-filter: brightness(80%);
            filter: brightness(80%);
    color: black;
    opacity: 0.7;
}

button:active {
    opacity: 0.4;
    -webkit-transform: translateY(0.3vh);
            transform: translateY(0.3vh);
}

.body {
    background-color: rgba(255,255,255, 0.2);
    background-image: none;
    margin-bottom: 4vh;
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
    height: 74vh;
    width : 74vw;
    padding-top: 3vh;
    padding-right: 3vw;
    padding-left:3vw;
    overflow: auto;
    border: 0.3vh solid rgba(255,255,255, 0.1);
}

.credits {
    font-size: 1vw;
    float:right;
}
.credits p {
    color:rgba(255,255,255, 0.6);
    margin: 1vh;
    padding: 0.4vw;
    border-radius: 1vw;
    font-size: 1vw;
}


a {
    color: inherit;
    font: inherit;
}

::-webkit-scrollbar {
    display: none;
  }

  @media only screen and (max-width:600px) {

    .menu {
        margin-left: 5%;
        margin-right: 5%;
        justify-content: center;
        height: 5vh;
    }
    .menu button {
        padding:0;
        height: 4vh;
        font-size: 2.2vw;
        width: 20vw;
        margin: 0;
    }
    .menu button:hover {
