.home-container .body {
    display: block;
}

.home-container .body .alert-content {
    border: 0.5vw solid rgba(255,255,255, 0.3);
    display: flex;
    justify-content:space-between;
    background-color: rgba(255,255,255, 0.3);
    padding-left: 2vw;
    padding-right: 2vw;
}

.home-container .body .alert-content h1 {
    display: flex;
    flex-wrap: wrap;
}

.home-container .body .alert-content span {
    padding-right: 2vw;
    justify-self: center;
}

.home-container .body .alert-content .close {
    align-self: center;
}
.close:hover {
    opacity:  50%;
    cursor: pointer;
}

.home-container .body .upper{
    height: 375px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.home-container .body .upper .profile {
    display:flex;
    align-items: center;
    flex-direction: column;
    margin-top: 15vh;
    border-radius: 2vw;
}

.home-container .body .upper .profile img{
    flex: left 1;
    max-width: 14vw;
    border-radius: 2vw;
    border: 2px solid rgba(247,195,181, 0.3);
}

.home-container .body .upper .profile p {
    -webkit-transform: translateY(-15%);
            transform: translateY(-15%);
