    text-align: center;
    border-radius: 1vw;
    background-color: rgba(247,195,181,0.3);
    padding: 1.5vh;
    display: flex;
    flex-wrap: wrap;
    width: 11vw;
}

.home-container .body .upper .citacao h1 {
    font: 3vw Cedarville Cursive;
    text-align: center;
    padding-top: 10vh;
    text-shadow: 0 0 1vw white;
}

.home-container .body .upper .citacao h4 {
    font: 1vw Bebas Neue;
    text-align: end;
    padding-right: 11vw;
    text-shadow: 0 0 5px white;
}

.home-container .body .redes {
    text-align: center;
    font: 200 0.8vw Roboto, sans-serif;
}
.home-container .body .redes p {
