.experiences-container .body .locais {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.experiences-container .body .locais .item {
  display: flex;
  padding: 10px;
  justify-content: center;
  align-content: center;  
}

.experiences-container .body .locais .item button {
  background: rgba(255,255,255,0.4);
  border: 0.1vw solid rgba(255,255,255, 0.1);
  border-radius: 30px;
  width: 300px;
  height: 140px;
  position: relative;
  max-width: 23vw;
  max-height: 21vh;
}

.experiences-container .body .locais .item button img {
  padding-top: 10px;
  height: 120px;
  max-height: 18vh;
  max-width: 23vw;
}

.experiences-container .body .locais .item button .overlay {
  -webkit-transform: translateY(-145px);
          transform: translateY(-145px);
  display: block;
  text-align: center;
  align-items: center;
  opacity: 0;
  transition: .5s ease;
  height: 0;
}

.experiences-container .body .locais .item button .overlay h4 {
    font:300 14px Open Sans Condensed;
    text-align: center;
}

.experiences-container .body .locais .item button .overlay h1 {
  font: 700 15px Open Sans Condensed;
  text-align: center;
}

.experiences-container .body .locais .item button:hover  {
  box-shadow: 2px 2px 20px black;
  -webkit-filter: none;
          filter: none;
  background: rgba(255,255,255,0.9);
}

.experiences-container .body .locais .item button:hover img {
  opacity: 0.1;
}

.experiences-container .body .curriculo {
  text-align: center;
  padding-top: 10px;

}

.experiences-container .body .curriculo img {
  box-shadow: 0 5px #999;
  border-radius: 9px;
}

.experiences-container .body .curriculo img:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  box-shadow: 0 3px #999;
}

.experiences-container div .modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  }

.experiences-container div .modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
}
  
  /* The Close Button */
.experiences-container .close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
  
.experiences-container .close:hover,
.experiences-container .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

@media only screen and (max-width:600px) {
  .experiences-container .body .locais {
    display: flex;
    flex-direction: column;
  }
  .experiences-container .body .locais .item button {
    max-width: 90%;    
  }
  .experiences-container .body .locais .item button img {
    max-width: 90%;
  }
}
